/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, FormGroup, Row } from 'reactstrap';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../constants/defaultValues';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import IntlMessages from '../../../helpers/IntlMessages';

import { wait } from '../../../helpers/Utils';

import 'react-datepicker/dist/react-datepicker.css';
import CustomDateInput from '../../../components/common/CustomDateInput';
import {
  getAllHackathons,
  updateCurrentHackathon,
} from '../../../redux/actions';
import useRequest from '../../../hooks/use-request';
import { NotificationManager } from '../../../components/common/react-notifications';

const sanitizeDate = (data) => {
  if (data) return new Date(data);

  return new Date();
};

const Dates = ({
  match,
  accessToken,
  updateAction,
  currentHackathon,
  refresh,
  loading,
}) => {
  const { id } = match.params;

  const history = useHistory();

  if (!id) {
    history.goBack();
  }

  useEffect(() => {
    updateAction(id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const {
    application_start,
    application_end,
    hackathon_start,
    hackathon_date,
    submission_date,
    results_date,
  } = currentHackathon;

  const [values, setValues] = useState(() => ({
    application_start: sanitizeDate(application_start),
    application_end: sanitizeDate(application_end),
    hackathon_start: sanitizeDate(hackathon_start),
    hackathon_date: sanitizeDate(hackathon_date),
    submission_date: sanitizeDate(submission_date),
    results_date: sanitizeDate(results_date),
  }));

  const [dataValues, setData] = useState({
    application_start,
    application_end,
    hackathon_start,
    hackathon_date,
    submission_date,
    results_date,
  });

  useEffect(() => {
    setValues({
      application_start: sanitizeDate(currentHackathon.application_start),
      application_end: sanitizeDate(currentHackathon.application_end),
      hackathon_start: sanitizeDate(currentHackathon.hackathon_start),
      hackathon_date: sanitizeDate(currentHackathon.hackathon_date),
      submission_date: sanitizeDate(currentHackathon.submission_date),
      results_date: sanitizeDate(currentHackathon.results_date),
    });

    setData({
      application_start,
      application_end,
      hackathon_start,
      hackathon_date,
      submission_date,
      results_date,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentHackathon]);

  const [doRequest, requestLoading] = useRequest({
    url: '/updateHackathon',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: { id, ...dataValues },
    nextSuccess: () => {
      NotificationManager.success(
        'Hackathon updated successfully!',
        'Hackathon Operation Success',
        3000,
        null,
        null,
        ''
      );
      refresh(accessToken, id);
      wait(1200, () => {
        history.push(`${adminRoot}/challenges/prizes/${id}`);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event, key, value) => {
    setValues({
      ...values,
      [key]: value ? event : event.target.value,
    });

    const d = `${event.getDate()}`.padStart(2, '0');
    const m = `${event.getMonth() + 1}`.padStart(2, '0');
    const y = event.getFullYear();

    setData({
      ...dataValues,
      [key]: value ? `${y}-${m}-${d}` : event.target.value.toJSON(),
    });
  };

  const handleSubmit = () => {

    if (
      dataValues.application_start === undefined||
    dataValues.application_end === undefined||
    dataValues.hackathon_start === undefined||
    dataValues.hackathon_date === undefined||
    dataValues.submission_date === undefined||
    dataValues.results_date === undefined
    ) {
      NotificationManager.error(
        'Please select all the dates ',
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  if (loading) return <div className="loading" />;

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.dates" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="10" className="mb-4">
          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-application-begins" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsStart
                      startDate={values.application_start}
                      endDate={values.application_end}
                      selected={values.application_start}
                      onChange={(date) =>
                        handleChange(date, 'application_start', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-application-ends" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsEnd
                      startDate={values.application_start}
                      endDate={values.application_end}
                      minDate={values.application_start}
                      selected={values.application_end}
                      onChange={(date) =>
                        handleChange(date, 'application_end', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-hackathon-begins" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsStart
                      minDate={values.application_end}
                      startDate={values.hackathon_start}
                      endDate={values.hackathon_date}
                      selected={values.hackathon_start}
                      onChange={(date) =>
                        handleChange(date, 'hackathon_start', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-hackathon-ends" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsEnd
                      minDate={values.hackathon_start}
                      startDate={values.hackathon_start}
                      endDate={values.hackathon_date}
                      selected={values.hackathon_date}
                      onChange={(date) =>
                        handleChange(date, 'hackathon_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.submission-date" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      minDate={values.hackathon_date}
                      selected={values.submission_date}
                      onChange={(date) =>
                        handleChange(date, 'submission_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.announcement-results" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      minDate={values.submission_date}
                      selected={values.results_date}
                      onChange={(date) =>
                        handleChange(date, 'results_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Colxx>
            <FormGroup className="text-center">
              <Button
                id="submit-links"
                type="submit"
                color="primary"
                className={`btn-shadow btn-multiple-state ${
                  requestLoading ? 'show-spinner' : ''
                }`}
                onClick={handleSubmit}
              >
                <span className="spinner d-inline-block">
                  <span className="bounce1" />
                  <span className="bounce2" />
                  <span className="bounce3" />
                </span>
                <span className="label">
                  <IntlMessages id="forms.update" />
                </span>
              </Button>
            </FormGroup>
          </Colxx>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/brands/${id}`)
              }
            >
              <IntlMessages id="forms.previous" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { currentHackathon, loading, error } = hackathonData;
  return {
    accessToken,
    currentHackathon,
    loading,
    error,
  };
};

export default connect(mapStateToProps, {
  updateAction: updateCurrentHackathon,
  refresh: getAllHackathons,
})(Dates);
